body{
    background: -webkit-linear-gradient(left, rgba(0,0,0,0.9), rgba(30,144,255,0.9)) ,url('../../images/hero-image.jpeg');
    background: linear-gradient(to right, rgba(0,0,0,0.9), rgba(30,144,255,0.9)) ,url('../../images/hero-image.jpeg');
    background-size:cover;
    background-position: center;
}
.header{
    
    color:#fff;
}

.header .hero-heading{
    letter-spacing: 1rem;
}

.contactList .contactList-content{
    max-width:55%;
    margin:0 auto;
}

.contactList .list-group-item{
    background:transparent;
    color:#f5f5f5;
    border:none;
    border-bottom: 1.5px solid #a1a1a1;
    font-size: 110%;
   
}


@media only screen and (max-width:768px){
    .contactList .contactList-content{
        max-width: 95%;
    }

}
.create-contact-container{
    max-width:40%;
    margin:0 auto;
}

.create-contact-container .contact-form{
    
    box-shadow: 2px 4px 16px #000;
    padding: 5%;
}


@media only screen and (max-width:768px){
    .create-contact-container{
        max-width: 95%;
    }

}
